<template>
  <div class='registration'>
      <Input
        label='Your product'
        placeholder='Enter your product'
        @input="check = $event"

      /> 

       <button
      @click='checkProduct'
    >
      Check product
    </button>
    <div v-html='text'></div>
  </div>
</template>

<script>
  import Input from './Input';
  export default {
     components:{
      Input
    },

    data(){
      return{
        check: '',
        text: ''
      }
    },

    methods:{
      checkProduct(){
        let id = parseInt(localStorage.getItem('id'));
        for(let i=1;i<=id;i++){
          let result = JSON.parse(localStorage.getItem(i));
          if(this.check.toLowerCase() === result.product){
            this.text = `Your product - <b>'${this.check.toLowerCase()}'</b> comes with the product - <b>'${result.product}'</b>. <br> auother -  <i> ${result.fullName}</i> <br> mail - <i>${result.mail}</i>`; 
            return;
          }
        }
        this.text = `Your product - <b>${this.check.toLowerCase()}</b> unique. Please Register your product`; 
      }
    }
  }
</script>

<style>

</style>


