<template>
    <div class="v-input">
      <label class='v-input__label'>
        <span>{{label}}</span>
        <div class="v-input__wrap">
          <input
            @input='input'
            :placeholder='placeholder'
            type="text"
          >
        </div>
      </label>
    </div>
</template>

<script>
  export default {
    props: ['label', 'placeholder'],
    methods:{
      input(e){
        this.$emit('input', e.target.value)
      }
    }
  }
</script>

<style>

</style>


