<template>
  <div id="app">
    <header class='header'>
      <span class='title'>Protec</span>
      <div class="switch">
        <ul>
          <li 
            v-for="item in formText" :key="item"
            :class="{active: currentTab === item}" 
            @click='switchTab(item)'
          >
            {{item}}
          </li>
        </ul>
      </div>
      </header>

    <main class='main'>
      <img src="./assets/logo.png" alt="Vue.js PWA">
      <Registarion v-if='currentTab === "Reg"'/> 
      <Audit v-else />
    </main>
  </div>
</template>

<script>
import Registarion from './components/Registarion'
import Audit from './components/Audit'

export default {
  name: 'app',
  components: {
    Audit, Registarion
  },
  data(){
    return{
      formText: ['Reg', 'Check'],
      currentTab: 'Reg',
    }
  },
  methods: {
    switchTab(tab){
      this.currentTab = tab
    }
  }
}
</script>

<style>
*{
  box-sizing: border-box;
}
body {
  margin: 0;
  height: 100vh;

}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  color: #2c3e50;
  height: 100%;
}

.main {
  text-align: center;
  padding-top: 40px;
  background-color: #41B883;
  height: 100%;
}
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #35495E;
  color: #ffffff;
  height: 60px;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding: 0 20px;
}
.switch ul{
  font-size: 15px;
  border-radius: 40px;
  margin: 0;
  width: 140px;
  height: 40px;
  display: flex;
  align-items: center;
  overflow: hidden;
  list-style-type: none;
  padding: 0;

}
.switch ul li{
  margin: 0;
  padding: 0;
  width: 50%;
  height: 100%;
  line-height: 40px;
  text-align: center;
  background-color: #4C5C75;
}
.switch ul li.active{
  background-color: #41B883;
}
.v-input{
  width: 200px;
  margin: 0 auto;
  text-align: left
}
label{
  font-weight: bold;
}
input{
  margin-top: 5px;
  padding: 5px;
  height: 30px;
  background-color: #4C5C75;
  width: 100%;
  border: 1px solid #4C5C75;
  margin-bottom: 20px;
  color: white;
  font-weight: bold;
}
input::placeholder{
  color: white;
}
button{
  margin-top: 20px;
  background-color: #35495E;
  width: 200px;
  height: 40px;
  text-align: center;
  line-height: 40px;
  text-transform: capitalize;
  color: #41B883;
  font-size: 18px;
  border: none;

}
</style>



