<template>
  <div class='registration' v-if="result">
    <Input
      label='Full name'
      placeholder='Enter full name'
      @input="fullName = $event"

    />
    <Input
      label='Email'
      placeholder='Enter email'
      @input="mail = $event"
    />
    <Input
      label='Your product'
      placeholder='Enter your product'
      @input="product = $event"

    />

    <button
      @click='registerProduct'
    >
      register product
    </button>
  </div>
   <div v-else class="result" >
    Sign up for product registration 
  </div>
</template>

<script>
import Input from './Input';
export default {
    components:{
      Input
    },

    data(){
      return{
        result: true,
        fullName: '',
        mail: '',
        product: '',
      }
    },
    methods:{
      registerProduct(){
        if(this.product==='' || this.mail==='' || this.fullName===''){
          return
        }
        this.result = false;

        let data = {
          fullName: this.fullName,
          mail: this.mail,
          product: this.product.toLowerCase()
        }
        let serialObj = JSON.stringify(data);
        let value = parseInt(localStorage.getItem('id')) + 1;
        localStorage.setItem('id', value)
        localStorage.setItem(value, serialObj)
        
      },
    }
  }
</script>

<style>

</style>


